import React from 'react';
import './style.less';

class Test extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        let {
            getActionOneText,
            actionOneText,
            getActionTwoText,
            actionTwoText,
            fetchText,
            fetchTextRequest,
            fetchTextSuccess,
            fetchDate
            } = this.props;

        return (
            <div className='test'>
                <button
                    onClick={getActionOneText}>ActionOne
                </button>
                <div>ActionOne:{actionOneText}</div>

                <button
                    onClick={getActionTwoText}>ActionTwo
                </button>
                <div>ActionTwo:{actionTwoText}</div>

                <button
                    onClick={fetchText}>fetchText
                </button>
                <div>:{fetchTextRequest}</div>
                <div>:{fetchTextSuccess},example:{fetchDate}</div>
            </div>
        );
    }
}

Test.propTypes = {
    getActionOneText: React.PropTypes.func.isRequired,
    actionOneText: React.PropTypes.string,
    getActionTwoText: React.PropTypes.func.isRequired,
    actionTwoText: React.PropTypes.string,
    fetchText: React.PropTypes.func.isRequired,
    fetchTextRequest: React.PropTypes.string,
    fetchTextSuccess: React.PropTypes.string,
    fetchDate: React.PropTypes.string
};

export default Test;


